/**
 * getRightGap    .
 * selection       .
 *       selection  .
 *   getRightGap .
 *
 * ex) <span>abcde</span> <- selection c   de   highlight .
 *
 * @param startContainer selection   
 * @param startOffset startContainer    selection  
 * @returns  
 */

export default function getRightGap(endContainer: Node, endOffset: number) {
    const endNode = endContainer.firstChild;

    if (!endNode) return 0;

    const wholeText = (endNode as Text).wholeText;
    const afterEndRange = new Range();
    afterEndRange.setStart(endNode, endOffset);
    afterEndRange.setEnd(endNode, wholeText.length);

    const { width } = afterEndRange.getBoundingClientRect();

    return width;
}
