import { SizeType } from '@/types/SizeType';

/**
 *   size  
 * @param el size  
 * @param size  
 */
export default function resizeElement(
    el: HTMLElement | undefined,
    size: SizeType
) {
    if (!el) return;

    el.style.width = Math.floor(size.width) + 'px';
    el.style.height = Math.floor(size.height) + 'px';
}
