/**
 * hasText Selection    .
 * selection     ,   undefined .
 *   blink       hasText .
 * @param range selection    Range 
 * @returns selection     boolean
 */
export default function hasText(range: Range) {
    const { startContainer, endContainer } = range;
    return (
        startContainer.nodeName === '#text' && endContainer.nodeName === '#text'
    );
}
